import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';

const nums = [
      {digit: 0,
       id: "zero"},
      {digit: 1,
       id: "one"},
      {digit: 2,
       id: "two"},
      {digit: 3,
       id: "three"},
      {digit: 4,
       id: "four"},
      {digit: 5,
       id: "five"},
      {digit: 6,
       id: "six"},
      {digit: 7,
       id: "seven"},
      {digit: 8,
       id: "eight"},
      {digit: 9,
       id: "nine"},
      {digit: ".",
       id: "decimal"}
    ];

function Power(props) {
  return (
    <div>
      <button className="btn power" onClick={props.handleOnOff}>On/Off</button>
    </div>
  );
}

function Clear(props) {
  return (
    <div>
      <button id="clear" className="btn" onClick={props.handleClear}>Clear</button>
    </div>
  );
}

function Number(props) {
  return (
    <div>
      <button className="btn" id={props.num.id} onClick={() => props.handleClick(props.num.id)}>{props.num.digit}</button>
    </div>
  );
}

function Display(props) {
  return (
    <div>
      <input type="text" id="display" name="display" value={props.value} />
    </div>
  );
}

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      display: "",
      calcVal: 0
    }
    this.handleOnOff = this.handleOnOff.bind(this);
    this.handleClear = this.handleClear.bind(this);
    this.handleNumClick = this.handleNumClick.bind(this);
  }
  handleOnOff() {
    let buttons = document.querySelectorAll("button:not(.power)");
    buttons.forEach(function(button) {
      if (!button.hasAttribute("disabled")) {
        button.setAttribute("disabled", "");
      } else {
          button.removeAttribute("disabled", "");
      }
    });
    this.handleClear();
  }
  handleClear() {
    this.setState({
      display: ""
    });
  }
  handleNumClick(id) {
    let valueClicked = document.getElementById(id).innerHTML;
    this.setState({
      display: this.state.display + valueClicked,
      calcVal: parseInt(this.state.display + valueClicked, 10)
    });
  }

  render() {
    const numComponents = nums.map(item => <Number key={item.id} num={item} handleClick={this.handleNumClick}/>);

    return (
      <div className="container">
        <Power handleOnOff={this.handleOnOff}/>
        <Clear handleClear={this.handleClear}/>
        {numComponents}
        <Display value={this.state.display} />
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));
